
























































import Vue from "vue";
import { Component } from "vue-property-decorator";
import Comments from "./Comments.vue";
@Component({
  components: {
    comments: Comments
  }
})
export default class SearchInfo extends Vue {
  items: Array<any> = [];
  value = "";
  dialog = false;
  dialogItem: any = {};

  search() {
    if (!this.value) {
      this.items = [];
      return;
    }
    fetch(`https://newsapp-api-qou7lgjz3a-uc.a.run.app/api/news/search/?q=${this.value}`, {
      method: "GET"
    })
      .then(data => data.json())
      .then(body => {
        this.items = body.result;
        // console.log(this.items);
      })
      .catch(err => {
        throw err;
      });

    // this.value = "";
  }

  modal(item: any) {
    this.dialogItem = item;
  }
}
