





































































































import Vue from "vue";
import { Component, PropSync } from "vue-property-decorator";
@Component
export default class Comments extends Vue { 
  name = "";
  body = "";
  comments: Array<Record<string, any>> = [];
  // A reactive prop. If the parent component is passing a value to the child componenet.
  //prop sync is like @prop but it creates a getter and setter behind the scene.
  @PropSync("dialog", { type: Boolean }) syncedDialog!: boolean;
  @PropSync("item", { type: Object }) syncedItem!: any;
  dialog2 = false;

  createComment() {
    if (this.name == "") {
      this.name = "Anonymous";
    }

    const data = {
      article: this.syncedItem.title,
      name: this.name,
      body: this.body
    };

    fetch("https://newsapp-api-qou7lgjz3a-uc.a.run.app/api/comment/post_comment", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(data)
    })
      .then(resp => resp.json())
      .then(data => {
        this.comments = this.comments.concat(data.comment);
      })
      .catch(err => {
        throw err;
      });
    this.name = "";
    this.body = "";
  }

  getComments() {
    fetch(
      `https://newsapp-api-qou7lgjz3a-uc.a.run.app/api/comment/get_comments/${this.syncedItem.title}`,
      {
        method: "GET"
      }
    )
      .then(data => data.json())
      .then(comment => {
        this.comments = comment.comments;
      })
      .catch(err => {
        throw err;
      });
  }

  resetDialog() {
    this.syncedItem = {};
  }
}
